import Layout from "../components/Layout.js";
//import FullContainer from "../components/FullContainer.js";

import React, { Component } from "react";
import Link from "next/link";
import fetch from "isomorphic-unfetch";
import Error from "next/error";
import PageWrapper from "../components/PageWrapper.js";
import Menu from "../components/Menu.js";
import CategoryTitle from "../components/CategoryTitle.js"
import { Config } from "../config.js";
import { Title, ContentWrapper } from "../style-config.js";
import ImageGrid from "../components/ImageGrid.js"
import Festivales from "../components/Festivales.js"
import Programacion from "../components/Programacion.js"


class Category extends Component {
    static async getInitialProps(context) {
      console.log('getting props from category')
        const { slug } = context.query;
        const categoriesRes = await fetch(
            `${Config.apiUrl}/wp-json/wp/v2/categories?slug=${slug}`
        );
        const categories = await categoriesRes.json();

        if (categories.length > 0) {
            const postsRes = await fetch(
                `${Config.apiUrl}/wp-json/wp/v2/posts?_embed&categories=${
                    categories[0].id
                }`
            );
            const posts = await postsRes.json();
            return { categories, posts, domeState: 'open' };
        }
        return { categories };
    }
    render() {
        if (this.props.categories.length == 0)
            return <Error statusCode={404} />;

        // const posts = this.props.posts.map((post, index) => {
        //     return (
        //         <ul key={index}>
        //             <li>
        //                 <Link
        //                     as={`/post/${post.slug}`}
        //                     href={`/post?slug=${post.slug}&apiRoute=post`}
        //                 >
        //                     <a>{post.title.rendered}</a>
        //                 </Link>
        //             </li>
        //         </ul>
        //     );
        // });
        console.log("category props", this.props)
        let body
        if (this.props.categories[0].slug === 'festivales') {
          body =     <Festivales posts={this.props.posts} />

        } else if (this.props.categories[0].slug === 'programacion'){
          body =    <Programacion posts={this.props.posts} />
        } else if (this.props.categories[0].slug === 'artistas'){
          body = <ImageGrid posts={this.props.posts} />
        }
        //<ImageGrid posts={this.props.posts} />
        //  <Festivales posts={this.props.posts} />
        return (
            <Layout>
                <Menu menu={this.props.headerMenu} />
                <div className={ContentWrapper}>
                  <CategoryTitle title={this.props.categories[0].name} />
                    {body}
                  </div>
            </Layout>
        );
    }
}

export default PageWrapper(Category);
